@import "../../global.scss";

.topbar {
    width: 100%;
    height: 80px;
    background-color: #A1C3D1;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 2;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}